$(".header-search__btn").click(function () {
    $("body").addClass("fixed-body")
    $(".header").addClass("_open-search")
})

$(".main-slide").click(function () {
    $("body").removeClass("fixed-body")
    $(".header").removeClass("_open-search")
})

$(document).mouseup( function(e){
    let div = $( ".header" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open-search")){
            $(div).removeClass("_open-search")
        }
    }
});

$("._toggle-menu").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
    $(".nav-item2").removeClass("_active")
    $(".header").removeClass("_open-submenu")
})

$(".nav-item2__show").mouseover(function () {
    if(window.innerWidth<=992){
        return
    }

    $(".nav-item2").removeClass("_active")
    $(this).parents(".nav-item2").addClass("_active")
})

$(".nav2__link").mouseover(function () {
    if(window.innerWidth<=992){
        return
    }

    $(".nav-item2").removeClass("_active")
})

$(".nav-item2__show").click(function () {
    if(window.innerWidth>992){
        return
    }

    $(".nav-item2").removeClass("_active")
    $(this).parents(".nav-item2").addClass("_active")
    $(".header").addClass("_open-submenu")
})


$(".nav-item2__back").click(function () {
    $(".nav-item2").removeClass("_active")
    $(".header").removeClass("_open-submenu")
})

